<template>
  <el-card class="demo-card" header="form-desc " shadow="never">
    <div class="form-desc-demo">
      <div style="width: 600px">
        <ele-form
          :formData="formData"
          :formDesc="formDesc"
          :request-fn="handleSubmit"
          :rules="rules"
          @request-success="handleSuccess"
        />
      </div>
      <div style="margin-left: 50px;width: 500px;">
        <codemirror @input="handleCodeChange" v-model="code" />
      </div>
    </div>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      rules: {
        title: [
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ]
      },
      formDesc: {},
      code: `{
  author: {
    children: {
      first: {
        type: 'input',
        label: '',
        layout: 6,
        required: true
      },
      second: {
        type: 'input',
        label: '',
        layout: 18,
        required: true,
        disabled: data => !data.author.first
      }
    }
  },
  title: {
    type: 'input',
    label: '',
    rules: [
      { required: true, message: '', trigger: 'blur' }
    ]
  },
  content: {
    type: 'textarea',
    label: '',
    required: true,
    attrs: {
      autosize: { minRows: 4, maxRows: 10 }
    }
  },
  original: {
    type: 'radio',
    label: '',
    options: [
      { text: '', value: 1 },
      { text: '', value: 0 }
    ]
  },
  reprinted: {
    type: 'switch',
    label: '',
    vif (data) {
      return data.original === 1
    }
  },
  sourceUrl: {
    type: 'input',
    label: '',
    vif (data) {
      return data.original === 0
    },
    slots: {
      prepend (h) {
        return h('span', 'http://')
      },
      append (h) {
        return h('span', '.com')
      }
    }
  }
}`,
      formData: {}
    }
  },
  methods: {
    handleSubmit(data) {
      /* eslint-disable */
      console.log(data)
      return Promise.resolve()
    },
    handleSuccess() {
      this.$message.success('')
    },
    handleCodeChange(code) {
      try {
        /* eslint-disable */
        const codeData = eval('(' + code + ')')
        this.formDesc = codeData
      } catch {}
    }
  },
  created() {
    this.handleCodeChange(this.code)
  }
}
</script>

<style>
.form-desc-demo {
  display: flex;
  justify-content: center;
}
.form-desc-demo .CodeMirror {
  height: 700px !important;
}
</style>
